/* === –û–±—â–∏–µ —Å—Ç–∏–ª–∏ –¥–ª—è body === */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: #121212;
  color: #ffffff;
  overflow: hidden;
  height: 100vh;
}

/* === –û–±—ë—Ä—Ç–∫–∞ –≤—Å–µ–≥–æ –∫–∞–ª–µ–Ω–¥–∞—Ä—è === */
.main-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* === –ó–∞–≥–æ–ª–æ–≤–æ–∫ –∫–∞–ª–µ–Ω–¥–∞—Ä—è === */
.calendar-header {
  padding: 6px 22px 0px;
  border-bottom: 1px solid #222;
}

.calendar-header h2 {
  font-size: 16px;
  font-weight: 800;
  margin: 0;
}

.calendar-header p {
  font-size: 12px;
  color: #999;
  margin-top: 0px;
}

/* === –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å –Ω–µ–¥–µ–ª—è–º–∏ === */
.week-container {
  flex-grow: 1;
  position: relative;
  overflow-y: auto; /* –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª, –µ—Å–ª–∏ –Ω–µ –≤–ª–µ–∑–∞–µ—Ç */
  padding: 0 16px 16px;
  box-sizing: border-box;
}

.week-block {
  position: relative; /* –±–æ–ª—å—à–µ –Ω–µ –∞–±—Å–æ–ª—é—Ç */
  width: 100%;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  background: rgba(30, 30, 30, 0.4);
  border-radius: 20px;
  padding: 0 0 12px 0;
  box-shadow: 0 4px 15px #000000cc;
  box-sizing: border-box;
}

/* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–∞—è –ª–∏–Ω–∏—è */
.week-block::before {
  content: '';
  position: absolute;
  left: 76px;   /* —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–æ–µ –º–µ—Å—Ç–æ –¥–ª—è –ª–∏–Ω–∏–∏ */
  top: 0px;
  bottom: 20px;
  width: 1px;
  background-color: #444;
  border-radius: 2px;
  z-index: 1;
}

/* –û–¥–∏–Ω –¥–µ–Ω—å –Ω–µ–¥–µ–ª–∏ */
.day-row {
  position: relative;
  height: 56px; /* —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è, –æ–¥–∏–Ω–∞–∫–æ–≤–∞—è –≤—ã—Å–æ—Ç–∞ */
  margin: 0;
  padding: 0 16px 0 12px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
  cursor: pointer;
  box-sizing: border-box;
}

/* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∞—è –ª–∏–Ω–∏—è */
.day-row::after {
  content: '';
  position: absolute;
  left: 60px; /* –ø–æ–¥ –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–π */
  right: 16px;
  bottom: -1px; /* –ù–ê–î —Å–ª–µ–¥—É—é—â–∏–º –±–ª–æ–∫–æ–º */
  height: 1px;
  background-color: #444;
  border-radius: 1px;
  z-index: 1;
}

/* –õ–∏–Ω–∏—è –Ω–µ –ø–æ—Å–ª–µ–¥–Ω–µ–º—É –¥–Ω—é */
.day-row:last-child::after {
  display: none; /* —É–±–∏—Ä–∞–µ–º –ø–æ—Å–ª–µ–¥–Ω—é—é —Ä–∞–∑–¥–µ–ª–∏—Ç–µ–ª—å–Ω—É—é */
}


/* –õ–µ–≤–∞—è —á–∞—Å—Ç—å —Å –¥–Ω–µ–º –∏ –¥–∞—Ç–æ–π */
.day-left {
  flex: 0 0 45px; /* —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  user-select: none;
  padding-left: 6px;
  box-sizing: border-box;
  text-align: center;
}

/* === –ù–∞–∑–≤–∞–Ω–∏–µ –¥–Ω—è === */
.day-name {
  color: #888;           /* –°–¥–µ–ª–∞–ª–∏ —Ü–≤–µ—Ç –ø–æ—Ç–µ–º–Ω–µ–µ –∏ –º–µ–Ω–µ–µ —è—Ä–∫–∏–º */
  font-weight: 600;
  font-size: 10px;       /* –£–º–µ–Ω—å—à–∏–ª–∏ —Ä–∞–∑–º–µ—Ä —Å 14px –¥–æ 10px */
  line-height: 1.2;
  padding: 0;
  text-align: center;
  user-select: none;
}

/* === –î–∞—Ç–∞ –ø–æ–¥ –Ω–∞–∑–≤–∞–Ω–∏–µ–º === */
.day-date {
  font-size: 11px;
  color: #ddd;
  line-height: 1.2;
  padding: 0px 6px;
  text-align: center;
}

/* === –í—ã–¥–µ–ª–µ–Ω–∏–µ —Å–µ–≥–æ–¥–Ω—è—à–Ω–µ–≥–æ –¥–Ω—è === */
.current-day .day-date {
  border: 1px solid #fff;
  background-color: #fff;
  border-radius: 50px;
  font-weight: 700;
  color: #000;
  padding: 2px 10px;
}

/* –ü–æ–ª–µ —Å–º–µ–Ω –∏ —Å–æ–±—ã—Ç–∏–π */
.day-events {
  flex-grow: 1;
  margin-left: -30px;      /* —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É –ª–µ–≤–æ–π —á–∞—Å—Ç—å—é –∏ –ø–æ–ª–µ–º */
  padding-left: 56px;     /* —á—Ç–æ–±—ã —Ç–µ–∫—Å—Ç –Ω–∞—á–∏–Ω–∞–ª—Å—è —Å–ø—Ä–∞–≤–∞ –æ—Ç –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–π –ª–∏–Ω–∏–∏ */
  color: #777;
  font-size: 13px;
  user-select: none;
  min-height: 40px;
  border-radius: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  z-index: 3000;
  pointer-events: auto;
  user-select: none;
}

.choice-modal {
  margin-top: 45px;
  position: absolute;
  background: #eee;
  color: #333;
  border-radius: 16px;
  padding: 18px 12px 14px 12px;
  display: flex;
  align-items: center;
  gap: 16px;
  box-shadow: 0 0 12px #00000033;
  min-width: 280px;
  max-width: 90vw;
  user-select: none;
  pointer-events: auto;
  z-index: 9999; /* –Ω–∞ –≤—Å—è–∫–∏–π —Å–ª—É—á–∞–π –≤—ã—à–µ –≤—Å–µ–≥–æ */
}


.choice-icon {
  position: absolute;
  top: -15px;
  left: 16px;
  font-size: 20px;
  padding: 4px;
  color: #eee;
  z-index: 2;
}

.choice-btn {
  flex: 1;
  background: transparent;
  border: none;
  font-size: 15px;
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 12px;
  transition: background-color 0.3s ease;
}

.plus {
  font-weight: 900;
  font-size: 12px;
  line-height: 1;
}

.choice-divider {
  width: 1px;
  height: 28px;
  background: #bbb;
}

.sun-icon-wrapper {
  position: relative;
  font-size: 24px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fcd34d; /* –º—è–≥–∫–∏–π –∂–µ–ª—Ç–æ–≤–∞—Ç—ã–π */
}

.sun-icon {
  font-size: 32px;
  filter: drop-shadow(0 0 4px rgba(255, 255, 255, 0.5));
}

.sun-day-number {
  position: absolute;
  font-size: 12px;
  font-weight: bold;
  color: #000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

@media (max-width: 480px) {
  .week-container {
    padding: 0 12px 12px;
  }

  .day-row {
    height: 52px;
    gap: 8px;
  }

  .day-name, .day-date {
    font-size: 10px;
  }

  .day-events {
    font-size: 12px;
  }
}


